/**
 * Caching strategies
 *  application: caches the ouput in the application cache spanning multiple request
 *  request: caches the output to the current request
 *  none: does not cache output
 *  instance: caches the output to the current rendering instance, e.g. client render / server render
 */
export type CacheType = 'none' | 'instance' | 'application' | 'request';

export interface ICacheItem<T = unknown> {
    Item: T;
    Error?: boolean;
}

export interface ICache {
    Get<T = unknown>(type: string, key: string): ICacheItem<T> | null;

    Put<T = unknown>(type: string, key: string, item: T): ICacheItem<T> | null;

    Value<T = unknown>(item: ICacheItem<T>): T;

    GetValue<T = unknown>(type: string, key: string): T | null;
}
